"use client";

import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import Link from "next/link";

export default function CustomerDashboard() {
  const { data: session, status } = useSession();
  const router = useRouter();

  useEffect(() => {
    if (status === "loading") return;

    if (!session) {
      router.push("/"); // not logged in â†’ go to login
    } else if (session.user.role !== "customer") {
      router.push("/unauthorized"); // wrong role â†’ go to unauthorized
    }
  }, [session, status, router]);

  if (status === "loading") {
    return <p className="text-center mt-10">Loading...</p>;
  }

  if (!session || session.user.role !== "customer") {
    return null; // show nothing while redirecting
  }

  return (
    <div className="min-h-screen bg-gray-100 flex flex-col">
      {/* Top Navbar */}
      <header className="bg-blue-600 text-white py-4 px-6 flex justify-between items-center">
        <h1 className="text-xl font-bold">â˜• Cafe Management</h1>
        <span>Hi, {session.user.name} ðŸ‘‹</span>
      </header>

      {/* Main Content */}
      <main className="flex-1 p-6 grid grid-cols-1 md:grid-cols-3 gap-6">
        <Link
          href="/customer/menu"
          className="bg-white p-6 rounded-lg shadow hover:shadow-lg transition"
        >
          <h2 className="text-lg font-bold">ðŸ“‹ View Menu</h2>
          <p className="text-gray-600 mt-2">Browse and order items</p>
        </Link>

        <Link
          href="/customer/orders"
          className="bg-white p-6 rounded-lg shadow hover:shadow-lg transition"
        >
          <h2 className="text-lg font-bold">ðŸ›’ My Orders</h2>
          <p className="text-gray-600 mt-2">Track your order history</p>
        </Link>

        <Link
          href="/customer/profile"
          className="bg-white p-6 rounded-lg shadow hover:shadow-lg transition"
        >
          <h2 className="text-lg font-bold">ðŸ‘¤ Profile</h2>
          <p className="text-gray-600 mt-2">Manage your account</p>
        </Link>
      </main>
    </div>
  );
}
